$container: 1320px;

* {
    box-sizing: border-box;
}

*::before,
*::after {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    color: #fff;
}

body {
    font-weight: 400;
    font-family: "Montserrat";
    color: #000;
    background: #282a30;
}

img {
    max-width: 100%;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

button {
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

a {
    text-decoration: none;
    color: #282a30;
}

.App {
    margin-bottom: 200px !important;
}

.unselectable-word {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -khtml-user-select: none; /* Konqueror */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently                                   not supported by any browser */
}

.be-relative {
    position: relative;
}

.container {
    max-width: $container;
    margin: 0 auto;
}

.stn-title {
    font-style: normal;
    font-weight: normal;
    font-size: 41px;
    line-height: 50px;
}

.stn-title_white {
    color: #fff;
}

.stn-title_black {
    color: #000;
}

.stn-text {
    font-weight: normal;
    font-size: 16px;
    line-height: 30px;
    color: #737373;
}

.visible {
    visibility: visible !important;
    opacity: 1 !important;
}

.d-flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    margin-top: 50px;
    margin-bottom: 100px;
}

.post__wrapper {
    margin-bottom: 30px;
}

.post__title {
    font-size: 20px;
    color: rgb(230, 74, 74);
}

.post__body {
    font-size: 16px;
    color: #fff;
}
@media (max-width: 1400px) {
    .container {
        $container: 1140px !global;
        max-width: $container;
    }
}

@media (max-width: 1200px) {
    .container {
        $container: 960px !global;
        max-width: $container;
    }
}

@media (max-width: 992px) {
    .container {
        $container: 720px !global;
        max-width: $container;
    }
}

@media (max-width: 768px) {
    .container {
        $container: 540px !global;
        max-width: $container;
    }
}

@media (max-width: 576px) {
    .container {
        max-width: 100%;
    }
}
